.visually-hidden {
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  transition: clip-path .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  overflow: hidden;
}

.modal-form__form-input:invalid, .modal-form__form-field-spam-input:invalid {
  border: 2px solid #ffc01d;
  border-radius: 14px;
  box-shadow: 1.751px 1.751px #ffc01d;
}

.modal-form__form-input:valid, .modal-form__form-field-spam-input:valid {
  border: 2px solid #9ed821;
  border-radius: 14px;
  box-shadow: 1.751px 1.751px #9ed821;
}

.modal-form__form-input:valid:after, .modal-form__form-field-spam-input:valid:after {
  content: "";
}

.modal-form__form-input:hover, .modal-form__form-input :focus, .modal-form__form-field-spam-input:hover, .modal-form__form-field-spam-input :focus {
  border: 2px solid #262c20;
  border-radius: 14px;
  text-decoration: none;
  box-shadow: 1.751px 1.751px #262c20;
}

.modal-form__form-field-female-radio:checked + .modal-form__form-field-female-svg, .modal-form__form-field-male-radio:checked + .modal-form__form-field-male-svg {
